body {
  background-color: hsl(212, 45%, 89%);
  font-size: 15px;
  font-family: 'Outfit', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
}

.card {
  background-color: hsl(0, 0%, 100%);
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
  border-radius: 0.625rem;
  max-width: 17rem;
  padding: 1rem;
  margin-bottom: 1rem;
  text-align: center;
}

.card img {
  width: 100%;
  border-radius: 0.625rem;
  max-height: 18rem;
  max-width: 18rem;
}

.card h1 {
  font-size: 1.375rem;
  font-weight: 700;
  color: hsl(218, 44%, 22%);
  margin: 1rem 0 0.5rem;
}

.card p {
  font-size: 0.94rem;
  font-weight: 400;
  color: hsl(220, 15%, 55%);
  margin: 0 0 1rem;
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}